.footer {
  width: 100%;
  min-height: 144px;
  padding: 66px 0 60px 0;
  box-sizing: border-box;
}

@media screen and (max-width:887px) {
  .footer {
    width: calc(100% - 38px);
  }
}

@media screen and (max-width:425px) {
  .footer {
    min-height: 98px;
    padding: 48px 0 36px 0;
  }
}
